@mixin font-desktop($fs: 16px, $fw: 500, $lh: 29.28px) {
  font-size: $fs;
  font-weight: $fw;
  line-height: calc($lh / $fs);
}

@mixin font-tablet($fs: 14px, $fw: 500, $lh: 25.62px) {
  font-size: $fs;
  font-weight: $fw;
  line-height: calc($lh / $fs);
}

@mixin font-mobile($fs: 15px, $fw: 500, $lh: 22.32px) {
  font-size: $fs;
  font-weight: $fw;
  line-height: calc($lh / $fs);
}

@mixin font-btn($fs: 14px, $fw: 600, $lh: 18px) {
  font-size: $fs;
  font-weight: $fw;
  line-height: calc($lh / $fs);
}

